var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"curso-main-container pb-3"},[_c('BannerInterno',{attrs:{"subTitulo":'1. Principios de planificación curricular en educación virtual'}}),_c('div',{staticClass:"container tarjeta tarjeta--blanca p-4 p-md-5 overflow-hidden"},[_vm._m(0),_vm._m(1),_vm._m(2),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-lg-7 col-xl-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Enfoque centrado en el estudiante"}},[_c('p',[_vm._v("En el corazón de la planificación curricular virtual reside el estudiante, erigiéndose como el protagonista indiscutible de su propio proceso de aprendizaje. En la educación virtual, este principio se traduce en el diseño de contenidos y actividades que fomenten activamente su autonomía, permitiéndole tomar decisiones sobre cómo, cuándo y a qué ritmo aprende. Se busca una profunda adaptación a los diversos estilos de aprendizaje (visual, auditivo, kinestésico, etc.), a los diferentes ritmos individuales y a las singulares trayectorias previas de cada estudiante. La meta es cultivar una participación activa y significativa, donde el estudiante no sea un mero receptor de información, sino un constructor activo de su propio conocimiento a través de experiencias que sean intrínsecamente retadoras, genuinamente colaborativas y sólidamente contextualizadas en su realidad (Silva Quiroz, 2017).")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Alineación constructiva"}},[_c('p',[_vm._v("La piedra angular de un diseño curricular robusto es la alineación constructiva, que establece una coherencia esencial entre los objetivos de aprendizaje que se persiguen, los contenidos disciplinares que se abordan, las metodologías pedagógicas que se emplean y los instrumentos de evaluación que se utilizan para medir el logro de dichos objetivos (Silva Quiroz, 2017). Cada componente del currículo debe apuntar inequívocamente hacia los mismos fines formativos, creando un sistema donde la enseñanza, el aprendizaje y la evaluación se refuerzan mutuamente. Esta profunda coherencia no solo facilita una medición precisa de los logros de aprendizaje de los estudiantes, sino que también proporciona una base sólida para la reflexión y el ajuste continuo de las estrategias pedagógicas cuando sea necesario.")])])])],1),_vm._m(3)]),_c('div',{staticClass:"row mb-4"},[_vm._m(4),_c('div',{staticClass:"col-12 col-lg-7 col-xl-8"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Flexibilidad y adaptabilidad"}},[_c('p',[_vm._v("La educación virtual, por su propia naturaleza, debe abrazar la flexibilidad y la adaptabilidad como principios rectores. Esto implica la capacidad de realizar ajustes continuos en respuesta a las dinámicas cambiantes del entorno de aprendizaje y a las características específicas del grupo de estudiantes. Un currículo virtual efectivo es aquel que puede modificarse con agilidad en función de las necesidades emergentes, sin perder de vista los resultados de aprendizaje previamente definidos (Alejos, 2023). "),_c('br'),_c('br'),_vm._v("La adaptabilidad también se manifiesta en la cuidadosa selección e incorporación de herramientas digitales que sean intrínsecamente accesibles y de fácil uso para todos los estudiantes, independientemente de sus habilidades tecnológicas previas.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Inclusión y accesibilidad"}},[_c('p',[_vm._v("Un diseño curricular para EVA debe ser un baluarte de la inclusión y la accesibilidad, asegurando que todos los estudiantes, sin excepción alguna por sus condiciones físicas, cognitivas, sociales o económicas, puedan participar en el proceso de aprendizaje en igualdad de condiciones. Este principio se materializa en la utilización de tecnologías que cumplan con los estándares de accesibilidad web (WCAG), en la presentación de contenidos que sean intrínsecamente comprensibles para una amplia gama de aprendices y en la implementación de metodologías pedagógicas que respeten y valoren la diversidad de la población estudiantil (Caballero, 2022).")])])])],1)]),_c('p',{staticClass:"mb-4"},[_vm._v("En la siguiente tabla, repase los principios de planificación curricular en educación virtual.")]),_vm._m(5),_vm._m(6),_c('div',{staticClass:"bg-full-width border-top-9-azul"},[_c('div',{staticClass:"px-5 pt-5 pb-0"},[_c('h2',[_vm._v("Material complementario")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-12 col-md-6 col-lg-8"},[_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3 mb-auto mt-1",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/book.svg"),"alt":"Icono libro"}}),_c('a',{staticClass:"text-small text-primary",attrs:{"target":"_blank","href":"http://repositorio.redrele.org/bitstream/24251239/57/1/Articulo_4_Warisata_N9V3.pdf"}},[_vm._v("Reyes, J. (2021). Planificación educativa: una necesidad para el docente.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3 mb-auto mt-1",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/link.svg"),"alt":"Icono enlace"}}),_c('a',{staticClass:"text-small text-primary",attrs:{"target":"_blank","href":"https://www.repo-ciie.dfie.ipn.mx/pdf/953.pdf"}},[_vm._v("Araque, I., Montilla, L., Meleán, R., & Arrieta, X. (2018). Hernández, J. A. M., Salgado, A. R. R., & Ayala, J. H. Innovación De Planes y Programas Basados En Competencias y En El Aprendizaje Centrado En El Alumno.")])]),_c('p',{staticClass:"d-flex my-4"},[_c('img',{staticClass:"me-3 mb-auto mt-1",style:({'max-width':'16px'}),attrs:{"src":require("@/assets/componentes/book.svg"),"alt":"Icono libro"}}),_c('a',{staticClass:"text-small text-primary",attrs:{"target":"_blank","href":"https://elibro.net/es/ereader/tecnologicadeloriente/272421"}},[_vm._v("Martínez, I. (Et al) (2024) Didáctica de la educación superior virtual: actualidad y perspectivas. Editori